import QrReader from 'react-qr-reader';

import './App.css';

function App() {
  const handleClick = () => {
    console.log('click');
  };

  const handleScan = data => console.log(data);

  const handleError = error => console.log(error);

  return (
    <>
      <button onClick={handleClick}>scan code</button>
      <QrReader
        delay={300}
        onScan={handleScan}
        onError={handleError}
      />
    </>
  );
}

export default App;
